@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;600;700;800&display=swap');

* {
  font-family: 'Fredoka', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #CFB186;
  --secondary-color: #5B3114;
  --tertiary-color: #A0662B;
  --contrast-color: #5E6B5E;
  --navbar-height: 5rem;
}

html, body{
  display: block;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  width: 100%;
}

body {
  background: var(--contrast-color);
  /* overflow-x: hidden; */
}

a {
  text-decoration: none;
}

::selection {
  background: #c77842;
}

.container {
  max-width: 1540px;
  margin: 0 auto;
  padding: 0 50px;
}

.inline-block {
  display: inline-block;
}

/* Animations */

@keyframes hover-animation {
  0% {
      transform: translateY(0);
  }
  50% {
      transform: translateY(10px); 
  }
  100% {
      transform: translateY(0); 
  }
}

@keyframes fade-in-animation {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0.5;
  }
  100% {
      opacity: 1;
  }
}



@keyframes fade-in-top-animation {
  0% {
      opacity: 0;
      height: 0;
  }
  50% {
      opacity: 0.5;
      height: 50%;
  }
  100% {
      opacity: 1;
      height: 100%;
  }
}

