.projects-section {
    padding: 15rem 0;
    position: relative;
    height: 100%;
    z-index: 1;
}

.projects {    
    display: flex;
    flex-direction: column;
    gap: 13rem;
    height: 100%;
}

.projects .project.reverse {
    grid-template-columns: 1fr 749px;
}

.projects-section::before {
    content: '';
    position: absolute;
    height: 0;
    width: 2px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-image: linear-gradient(#4b574b 45%, rgba(255,255,255,0) 0%);
    background-position: right;
    background-size: 10px 3rem;
    background-repeat: repeat-y;
    background-attachment: fixed;
    animation: fade-in-top-animation 1s forwards;
}

.projects-title {
    content: 'My projects';
    font-weight: 400;
    position: absolute;
    font-size: 1.3rem;
    color: #fff;
    top: 6.32rem;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    background: var(--contrast-color);
    opacity: 0;
    animation: fade-in-animation 0.3s forwards;
    animation-delay: 1s;
}

.projects .project {
    display: grid;
    grid-template-columns: 936px 1fr;
    position: relative;
    height: 100%;
    gap: 5rem;
    align-items: end;
}

.projects .project .project-card {
    box-shadow: 0 20px 80px 0 rgb(0 0 0 / 45%);
    height: 480px;
    width: 100%;
    position: relative;
    transition-timing-function: cubic-bezier(0.51, -0.5, 0.28, 1.36);
}

.projects .project .project-card .project-number {
    font-family: League Spartan, sans-serif;
    font-weight: 700;
    color: var(--primary-color);
    font-size: 12rem;
    position: absolute;
    z-index: 2;
    top: -40px;
    left: -40px;
}

.projects .project .project-card .project-link {
    font-family: League Spartan, sans-serif;
    font-weight: 700;
    background: var(--primary-color);
    color: #fff;
    text-decoration: none;
    display: inline-block;
    padding: 1rem;
    position: relative;
    border-radius: 4px;
    bottom: 20px;
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.projects .project .project-card .project-link svg {
    position: relative;
    top: 3.5px;
}

.projects .project .project-card .project-link:hover {
    background: var(--tertiary-color);
}

.projects .project .project-card:hover ~ .project-details-wrapper .project-details {
    opacity: 1;
    visibility: visible;
    transition: all 0.5s ease-in-out;
}

.projects .project .project-card:hover {
    box-shadow: 0 20px 80px 0 rgb(0 0 0 / 65%);
    transition: all 0.3s ease-in-out;
}

.projects .project .project-card:hover .project-title {
    transform: scale(1.02);
    transition: all 0.3s ease-in-out;
}

.projects .project .project-card:hover .project-title .project-arrow {
    transform: translateX(50px);
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.projects .project .project-card:hover .project-title .project-link {
    transform: translateX(50px);
    opacity: 1;
    transition: all 0.5s ease-in-out;
}

.projects .project.reverse .project-card:hover .project-title .project-arrow {
    transform: translateX(-50px);
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.projects .project.reverse .project-card:hover .project-title .project-link {
    transform: translateX(-50px);
    opacity: 1;
    transition: all 0.5s ease-in-out;
}


.projects .project .project-card .project-image {
    height: 100%;
}

.projects .project .project-card .project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50%;
    filter: brightness(0.65);
}

.projects .project .project-card .project-title {
    padding: 1.3rem;
    position: absolute;
    bottom: 27px;
    right: 37px;
    max-width: 700px;
    transform: scale(1);
    transition: all 0.3s ease-in-out;
    text-align: end;
}

.projects .project  .project-card .project-title h1 {
    font-size: 3rem;
    font-weight: 600;
    color: #fff;
    margin-bottom: 0.2rem;
}

.projects .project .project-card .project-title p {
    font-size: 1.3rem;
    font-weight: 400;
    color: #fff;
    font-style: italic;
    margin-bottom: 1rem;
}

.projects .project .project-card .project-title .project-arrow {
    font-size: 3rem;
    color: var(--primary-color);
    transition: all 0.5s ease-in-out;
}

.projects .project .project-details-wrapper .project-details::before {
    content: ' ';
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50px;
    left: -20px;
    background-color: var(--secondary-color);
    transform: rotate(45deg);
    z-index: -1;
}

.projects .project .project-details-wrapper {
    width: 100%;
    position: relative;
    top: 20%;
}

.projects .project .project-details-wrapper .project-details {
    width: 100%;
    background-color: var(--secondary-color);
    padding: 1.5rem;
    box-shadow: 2px 2px 6px 0px #00000070;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.projects .project .project-details-wrapper .project-details .project-desc {
    font-size: 1.2rem;
    text-align: justify;
}

.projects .project .project-details-wrapper .project-details .project-desc-footer {
    text-align: end;
    margin-top: 0.5rem;
    padding-top: 1rem;
}

.projects .project .project-details-wrapper .project-details .project-desc-footer svg {
    color: #fff;
    font-size: 1.5rem;
    margin: 0 0.4rem;
}

/* Reversed card */

.projects .project.reverse {
    grid-template-columns: 1fr 936px;
}

.projects .project.reverse .project-card {
    order: 1;
}

.projects .project.reverse .project-card .project-number {
    right: -40px;
    left: unset;
}

.projects .project.reverse .project-card .project-title {
    right: unset;
    left: 37px;
    text-align: start;
}

.projects .project.reverse  .project-card .project-title h1 {
    font-size: 3rem;
    font-weight: 600;
    color: #fff;
    margin-bottom: 0.2rem;
}

.projects .project.reverse .project-details-wrapper .project-details::before {
    content: ' ';
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50px;
    left: unset;
    right: -20px;
    background-color: var(--secondary-color);
    transform: rotate(45deg);
    z-index: -1;
}

.projects .project.reverse .project-details-wrapper .project-details {
    width: 100%;
    height: max-content;
    background-color: var(--secondary-color);
    position: relative;
    top: 20%;
    padding: 1.5rem;
    box-shadow: 2px 2px 6px 0px #00000070;
    color: #fff;
    opacity: 0;
    transition: all 0.3s ease-in-out;
}

.projects .project.reverse .project-details-wrapper .project-details .project-desc {
    font-size: 1.2rem;
    text-align: justify;
}

.projects .project.reverse .project-details-wrapper .project-details .project-desc-footer {
    text-align: end;
    margin-top: 0.5rem;
    padding-top: 1rem;
}

.projects .project.reverse .project-details-wrapper .project-details .project-desc-footer svg {
    color: #fff;
    font-size: 1.5rem;
    margin: 0 0.4rem;
}

/* Responsive  */

@media (max-width: 1420px) {
    .projects .project {
        grid-template-columns: 750px 1fr;
    }
    .projects .project.reverse {
        grid-template-columns: 1fr 750px;
    }
}

@media (max-width: 1290px) {
    .projects .project {
        grid-template-columns: 1fr;
    }
    .projects .project.reverse {
        grid-template-columns: 1fr;
    }
    .projects .project.reverse .project-card {
        order: unset;
    }
    
    .projects .project .project-details-wrapper {
        width: 100%;
        position: relative;
        top: unset;
    }

    .projects .project .project-details-wrapper .project-details::before,
    .projects .project.reverse .project-details-wrapper .project-details::before {
        top: -20px;
        left: unset;
        right: 35px
    }

    .projects {
        gap: 5rem;
    }

    .projects .project .project-card ~ .project-details-wrapper .project-details {
        opacity: 1;
        visibility: visible;
    }

    .projects .project.reverse .project-card .project-title .project-arrow {
        transform: translateX(-50px);
        opacity: 0;
    }

    .projects .project .project-card .project-title .project-arrow {
        transform: translateX(-50px);
        opacity: 0;
    }

    .projects .project .project-card .project-title .project-link {
        transform: translateX(50px);
        opacity: 1;
    }

    .projects .project.reverse .project-card .project-title .project-link {
        transform: translateX(-50px);
        opacity: 1;
    }   
}

@media (max-width: 430px) {

    .projects .project .project-card .project-title,
    .projects .project.reverse .project-card .project-title {
        padding: 10px;
        right: unset;
        left: unset;
    }

    .projects .project .project-card .project-title h1,
    .projects .project.reverse .project-card .project-title h1 {
        font-size: 3rem;
        line-height: 3rem;
    }

    .projects .project .project-card .project-title p,
    .projects .project.reverse .project-card .project-title p {
        font-size: 1.3rem;
    }

    .projects .project .project-card .project-number {
        font-size: 8rem;
    }

    .projects .project .project-card {
        height: 420px;
    }

}