.about-section {
    background: var(--primary-color);
}

.about {
    padding-top: 7rem;
    padding-bottom: 7rem;
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    gap: 3rem;
    align-items: center;
}

.about .about-left h1 {
    color: var(--secondary-color);
    font-size: 10rem;
    line-height: 4rem;
    text-align: end;
    margin-top: -0.8rem;
}

.about .about-right p {
    color: #fff;
    font-size: 1.7rem;
    text-align: justify;
}

@media (max-width: 960px) {
    .about {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 530px) {
    .about {
        grid-template-columns: 1fr;
    }

    .about .about-left h1 {
        font-size: 6rem;
        line-height: 2.3rem;
    }

    .about .about-right p {
        font-size: 1.3rem;
    }
}