.navbar-section {
    position: relative;
    z-index: 999;
}

.navbar {
    z-index: 999;
    width: 100%;
    height: var(--navbar-height);
    display: flex;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); 
}

.navbar .navbar-left {
    width: 400px;
    height: 100%;
    display: flex;
    align-items: center;
}

.navbar .navbar-left .text-logo {
    color: var(--secondary-color);
    line-height: 1.6rem;
    font-weight: 600;
    font-size: 1.7rem;
}

.navbar .navbar-right {
    height: 100%;
    margin-left: auto;
    display: flex;
    align-items: center;
}

.navbar .navbar-right .navbar-items {
    display: flex;
    gap: 2rem;
}

.navbar .navbar-right .navbar-items .navbar-item {
    text-align: center;
    cursor: pointer;
    font-weight: 500;
    font-size: 1.5rem;
    color: var(--secondary-color);
    white-space: nowrap;
    transition: all 0.3s ease-in-out;
}

.navbar .navbar-right .navbar-items .navbar-item:hover {
    color: #fff;
    transition: all 0.3s ease-in-out;
}

/* Responsive */

@media (max-width: 960px) {
    .navbar .navbar-right .navbar-items {
        display: none;
    }
}