/* Hero container */

.hero {
    background: var(--primary-color);
    height: 760px;
}

.hero .hero-container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

.hero .hero-container {
    width: 100%;
    display: flex;
}

/* Hero container left */

.hero .hero-container .hero-left {
    width: 60%;
}

.hero .hero-container .hero-left h1 {
    color: var(--secondary-color);
    font-size: 6rem;
    line-height: 5.2rem;
}

.hero .hero-container .hero-left p {
    color: #fff;
    font-weight: 600;
    font-size: 4rem;
    line-height: 4rem;
}

/* Hero container right */

.hero .hero-container .hero-right {
    position: relative;
    height: 100%;
    width: 40%;
}

.hero .hero-container .hero-right .social-wrapper {
    position: absolute;
    animation: hover-animation 4s ease-in-out infinite;
    z-index: 2;
}

.hero .hero-container .hero-right .social-wrapper .social-block {
    width: 250px;
    height: 250px;
    margin-bottom: 10px;
    background: var(--secondary-color);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 3px 12px 0px #00000070;
}

.hero .hero-container .hero-right .social-wrapper:hover .social-block {
    background: var(--tertiary-color);
    color: var(--secondary-color);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(1) {
    top: 150px;
    right: 33px;
    animation-delay: 1s;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(1) .social-block {
    transform: rotate(-20deg) scale(1);
    transition: all 0.3s ease-in-out;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(1):hover .social-block {
    transform: rotate(-20deg) scale(1.1);
}

.hero .hero-container .hero-right .social-wrapper:nth-child(2) {
    top: 370px;
    right: 305px;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(2) .social-block {
    transform: rotate(10deg) scale(1);
    transition: all 0.3s ease-in-out;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(2):hover .social-block {
    transform: rotate(10deg) scale(1.1);
}

.hero .hero-container .hero-right .social-wrapper:nth-child(3) {
    top: 640px;
    right: 33px;
    animation-delay: 2s;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(3) .social-block {
    transform: rotate(-15deg) scale(1);
    transition: all 0.3s ease-in-out;
}

.hero .hero-container .hero-right .social-wrapper:nth-child(3):hover .social-block {
    transform: rotate(-15deg) scale(1.1);
}

.hero .hero-container .hero-right .social-wrapper .social-block svg {
    font-size: 15rem;
}

/* Responsive  */

@media (max-width: 1520px) {
    .hero .hero-container .hero-left {
        width: 40%;
    }

    .hero .hero-container .hero-right {
        width: 60%;
    }
}


@media (max-width: 1140px) {
    .hero .hero-container .hero-left {
        width: 40%;
    }

    .hero .hero-container .hero-right {
        width: 60%;
    }

    .hero .hero-container .hero-right .social-wrapper .social-block {
        width: 200px;
        height: 200px;
    }

    .hero .hero-container .hero-right .social-wrapper:nth-child(2) {
        right: 250px;
    }

    .hero .hero-container .hero-right .social-wrapper:nth-child(3) {
        top: 600px;
    }

    .hero .hero-container .hero-left h1 {
        font-size: 5rem;
        line-height: 4.7rem;
    }

    .hero .hero-container .hero-left p {
        font-size: 3rem;
        line-height: 3rem;
    }
}

@media (max-width: 960px) {

    .hero .hero-container {
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
    }

    .hero .hero-container .hero-left,
    .hero .hero-container .hero-right {
        width: 100%;
    }

    .hero .hero-container .hero-right {
        display: flex;
        height: unset;
        gap: 1rem;
    }

    .hero .hero-container .hero-right .social-wrapper {
        position: unset;
        animation: unset;
        top: unset !important;
        right: unset !important;
        left: unset !important;
        bottom: unset !important;
        display: inline-block;
    }

    .hero .hero-container .hero-right .social-wrapper .social-block svg {
        font-size: 5.8rem;
    }

    .hero .hero-container .hero-right .social-wrapper .social-block {
        transform: unset !important;
        transition: unset;
        width: 100px;
        height: 100px;
    }
}

@media (max-width: 430px) {
    .hero {
        height: 550px;
    }

    .hero .hero-container .hero-left h1 {
        font-size: 4rem;
        line-height: 3.7rem;
    }

    .hero .hero-container .hero-left p {
        font-size: 2.1rem;
        line-height: 3rem;
    }

    .hero .hero-container .hero-right .social-wrapper .social-block svg {
        font-size: 4.8rem;
    }

    .hero .hero-container .hero-right .social-wrapper .social-block {
        transform: unset !important;
        transition: unset;
        width: 80px;
        height: 80px;
    }

}